<template>
    <el-container>
        <left-aside/>
        <el-container>
            <!--<el-header></el-header>-->
            <el-main>
                <el-row type="flex" justify="space-between">
                    <el-col :span="5">
                        <div style="font-size: 20px;height: 37px">
                            iFun
                        </div>
                    </el-col>
                </el-row>

                <filter-list listType="fun" :filterList="filterList" @filterAllList="filterFunList"
                             :label-list="labelList"/>

                <lesson-list lesson-type="fun" :type-list="iFunTypeList" :lesson-list="funList"
                             edit-page="/showIFunInfo/" @delLessonInfo="delLessonInfo" @handleCommand="handleCommand"/>
            </el-main>
            <el-footer style="text-align: center">
                <el-pagination
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="paginationSize"
                        layout="total, prev, pager, next"
                        :total="paginationNum">
                </el-pagination>
            </el-footer>

            <release-timing :isShow="isShowSetTime" :id="currentId" @hideTiming="hideTimer"
                            @releaseTimer="releaseTimer"/>
        </el-container>
    </el-container>
</template>

<script>
    import FilterList from '../components/FilterListItem'
    import ReleaseTiming from '../components/ReleaseTimingItem'
    import LeftAside from '../components/LeftAsideItem'
    import LessonList from '../components/LessonListItem'

    export default {
        components: {
            FilterList,
            ReleaseTiming,
            LeftAside,
            LessonList,
        },
        data() {
            return {
                iFunTypeList: {
                    funny: '搞笑',
                    movie: '电影',
                    entertainment: '娱乐',
                    culture: '文化',
                    travel: '旅行',
                    arts: '艺术',
                    foods: '美食',
                    sports: '运动',
                    technology: '科技',
                },
                funList: [],
                currentPage: 1,
                isShowSetTime: false,
                paginationNum: 0,
                paginationSize: 20,
                filterList: {},
                currentId: '',
                labelList: [],
            }
        },
        mounted() {
            this.init();
        },
        methods: {
            init() {
                this.getFunListData();
                this.getAllLabelData();
            },
            getFunListData(find) {
                find = find ? find : {};
                find.category = 'iFun';

                this.getFunNumData(find).then(() => {
                    find.page = this.currentPage;
                    find.num = this.paginationSize;

                    this.$axios.get('/ifun/_find', {params: find}).then((res) => {
                        console.log(res)
                        if (!res.data.code) {
                            this.funList = res.data.data
                        }
                    })
                });
            },
            getFunNumData(find) {
                return new Promise((resolve, reject) => {
                    this.$axios.get('/ifun/_getnum', {params: find}).then((res) => {
                        this.paginationNum = res.data.data;
                        resolve()
                    })
                })
            },
            filterFunList(list) {
                let data = {
                    page: 1,
                };

                if (list.title) {
                    data.character = list.title;
                }
                if (list.class) {
                    data.class = list.class;
                }
                if (list.label) {
                    data.label = list.label;
                }
                if (list.difficulty !== 'all') {
                    data.difficulty = list.difficulty;//难度
                }
                if (list.status !== 'all') {
                    data.status = list.status;
                }
                this.getFunListData(data);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getFunListData();
            },
            jumpPage(key) {
                this.$router.push(key)
            },
            handleCommand(command) {
                if (command.key === 'time') {
                    this.isShowSetTime = true;
                    this.currentId = command.value._id;
                } else {
                    this.postUpdateFunData(command.value);
                }
            },
            hideTimer() {
                this.isShowSetTime = false;
                this.init();
            },
            postUpdateFunData(command) {
                let data = new FormData();
                data.append('lessonId', command._id);

                this.releaseTimer(data);
            },
            delLessonInfo(id) {
                this.$confirm('文件将被直接删除', '确定删除？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                    confirmButtonClass: 'el-button el-button--danger'
                }).then(() => {
                    this.delLessonData(id)
                }).catch(() => {
                    // this.$message({type: 'info', message: '已取消删除'});
                });
            },
            delLessonData(id) {
                this.$axios.get('/ifun/_delete', {params: {lessonId: id}}).then((res) => {
                    if (!res.data.code) {
                        this.$message({type: 'success', message: '已删除'});
                        this.init();
                    }
                })
            },
            getAllLabelData() {
                this.$axios.get('/label/_find', {params: {category: 'iFun', all: 'all'}}).then((res) => {
                    console.log(res.data.data)
                    if (!res.data.code) {
                        this.labelList = res.data.data
                    }
                })
            },
            releaseTimer(data) {
                this.$axios.post('/ifun/' + data.get('lessonId') + '/release', data).then((res) => {
                    if (!res.data.code) {
                        this.$message.success('设置成功');
                        this.hideTimer();
                    }
                })
            }
        },

    }
</script>

<style scoped>
    .el-dropdown-link {
        cursor: pointer;
        color: #409eff;
        font-size: 12px
    }

    .dropdown-edit {
        margin-left: 15px
    }

    .table-secondary {
        font-size: 12px;
        color: #909399
    }
</style>